import { Component } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AnimePage } from '../anime/anime';

/**
 * Generated class for the AnimesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-animes',
  templateUrl: 'animes.html',
})
export class AnimesPage {

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private http: HttpClient
  ) {
  }

  public animesList;

  ionViewDidLoad() {
    const letter = this.navParams.get('letter');
    this.getAnimesByLetter(letter);
  }

  private getAnimesByLetter(letter) {
    this
      .http
      .get(
        `http://seis.techrevolution.com.br/odata/Animesdb?$filter=startswith(Nome,%27${letter.toUpperCase()}%27)&$select=Id,Nome,Imagem&$orderby=Nome&$skip=0&$inlinecount=allpages`
      ).subscribe((data: any) => {
      this.animesList = data.value;
    });
  }

  public goToAnime(id) {
    this.navCtrl.push(AnimePage, {id: id});
  }

}



