import { Component } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NavController, NavParams } from 'ionic-angular';
import { EpisodePage } from '../episode/episode';

@Component({
  selector: 'page-anime',
  templateUrl: 'anime.html'
})
export class AnimePage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private http: HttpClient
  ) {}

  public anime;
  public id;

  ionViewDidLoad() {
    this.id = this.navParams.get('id');
    this.getAnimeById(this.id);
  }


  private getAnimeById(id) {
    this
      .http
      .get(
        `http://seis.techrevolution.com.br/api/episodioexes/${id}`
      ).subscribe((data: any) => {
      this.anime = data;
    });
  }

  public episodeSelected(episode) {
    this.navCtrl.push(EpisodePage, {episode: episode});
  }
}



