import { Component } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the EpisodePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-episode',
  templateUrl: 'episode.html',
})
export class EpisodePage {

  public episode;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private http: HttpClient
  ) {
    this.episode = this.navParams.get('episode');
  }

  public videoLinks;

  ionViewDidLoad() {

    this.getEpisodeById(this.episode.Id);
  }

  private getEpisodeById(id) {
    this
      .http
      .get(
        `http://seis.techrevolution.com.br/api/episodioexes/links?id=${id}`
      ).subscribe((data: any) => {
      this.videoLinks = data;
      console.log(this.videoLinks);
    });
  }

}



